import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    login:false,
    complete:false
}

const mutations= {
    isLogin(state){
        state.login = true
    },
    iscomplete(state){
        state.complete = true
    },
    nocomplete(state){
        state.complete = false
    }
}

export default new Vuex.Store({
    state,
    mutations
})


