<template>
  <div>
    <appheader></appheader>
    <div class="container">
      <div class="Appdisplay">
        <div class="edge">
          <div class="desktop">
            <div class="showplace"
    element-loading-text="音频加载中"
    element-loading-background="rgba(0, 0, 0, 0.8)">
              <audio controls ref="audio">
                <source src="../../static/audio/audio-0.mp3" type="audio/mpeg">
              </audio>
            </div>
            <span class="detail">{{detail}}</span>
          </div>
        </div>

      </div>
      <div class="setgradewarp"  v-if="isinput" >
        <span>当前内容分数：</span>
        <input class="setgradeinput" placeholder="输入分数0~100" v-model="newgrade"  :disabled='disinput'>
        <el-button type="primary" @click="setgradeDone" v-if="isok">确定</el-button>
      </div>
      <v-table class="gradetable" :width="400" :columns="columns" :table-data="tableData" :show-vertical-border="false">
      </v-table>
    </div>
    <el-button class="uploadgrade" v-if="isbtnshow" @click="goVideo" type="success">提交所有分数</el-button>
  </div>
</template>
<script>
import appheader from "./AppHeader";
const musicList = [
  "./static/audio/audio-0.mp3",
  "./static/audio/audio-10.wav",
  "./static/audio/audio-25.wav",
  "./static/audio/audio-50.wav",

  "./static/audio/audio-0_500.wav",
  "./static/audio/audio-10_500.wav",
  "./static/audio/audio-25_500.wav",
  "./static/audio/audio-50_500.wav",

  "./static/audio/audio-0_1000.wav",
  "./static/audio/audio-10_1000.wav",
  "./static/audio/audio-25_1000.wav",
  "./static/audio/audio-50_1000.wav",

  "./static/audio/audio-0_1500.wav",
  "./static/audio/audio-10_1500.wav",
  "./static/audio/audio-25_1500.wav",
  "./static/audio/audio-50_1500.wav"
];
export default {
  name: "Audio",
  data() {
    return {
      tableData: [
        //app数据
        { qualityLossRatio: "0", time: "0", grade: "" },
        { qualityLossRatio: "10", time: "0", grade: "" },
        { qualityLossRatio: "25", time: "0", grade: "" },
        { qualityLossRatio: "50", time: "0", grade: "" },

        { qualityLossRatio: "0", time: "500", grade: "" },
        { qualityLossRatio: "10", time: "500", grade: "" },
        { qualityLossRatio: "25", time: "500", grade: "" },
        { qualityLossRatio: "50", time: "500", grade: "" },

        { qualityLossRatio: "0", time: "1000", grade: "" },
        { qualityLossRatio: "10", time: "1000", grade: "" },
        { qualityLossRatio: "25", time: "1000", grade: "" },
        { qualityLossRatio: "50", time: "1000", grade: "" },

        { qualityLossRatio: "0", time: "1500", grade: "" },
        { qualityLossRatio: "10", time: "1500", grade: "" },
        { qualityLossRatio: "25", time: "1500", grade: "" },
        { qualityLossRatio: "50", time: "1500", grade: "" }
      ],
      columns: [
        {
          field: "qualityLossRatio",
          title: "质量损失%",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          formatter(rowData, rowIndex, pagingIndex, field) {
            return `<span class='cell-edit-color'>${rowData[field]}</span>`;
          },
          isResize: true
        },
        {
          field: "time",
          title: "时间延迟ms",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "grade",
          title: "评分：",
          width: 200,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        }
      ],
      gradelen: 0,
      dataarr: [],
      reg: /^(0|[1-9][0-9]*)$/,
      newgrade: "",
      isbtnshow: false,
      isinput: true,
      index: 0,
      url: "",
      appdetail: [
        {
          title: "质量损失0% 时间延迟0ms",
          time: 0
        },
        {
          title: "质量损失10% 时间延迟0ms",
          time: 0
        },
        {
          title: "质量损失25% 时间延迟0ms",
          time: 0
        },
        {
          title: "质量损失50% 时间延迟0ms",
          time: 0
        },
        {
          title: "质量损失0% 时间延迟500ms",
          time: 500
        },
        {
          title: "质量损失10% 时间延迟500ms",
          time: 500
        },
        {
          title: "质量损失25% 时间延迟500ms",
          time: 500
        },
        {
          title: "质量损失50% 时间延迟500ms",
          time: 500
        },
        {
          title: "质量损失0% 时间延迟1000ms",
          time: 1000
        },

        {
          title: "质量损失10% 时间延迟1000ms",
          time: 1000
        },
        {
          title: "质量损失25% 时间延迟1000ms",
          time: 1000
        },
        {
          title: "质量损失50% 时间延迟1000ms",
          time: 1000
        },
        {
          title: "质量损失0% 时间延迟1500ms",
          time: 1500
        },

        {
          title: "质量损失10% 时间延迟1500ms",
          time: 1500
        },

        {
          title: "质量损失25% 时间延迟1500ms",
          time: 1500
        },

        {
          title: "质量损失50% 时间延迟1500ms",
          time: 1500
        }
      ],
      appitem: 0,
      isok: true,
      disinput: false,
      alldata: ""
    };
  },
  components: {
    appheader
  },
  computed: {
    detail() {
      return this.appdetail[this.appitem].title;
    }
  },
  methods: {
    setgradeDone() {
      if (
        !this.reg.test(this.newgrade) ||
        this.newgrade > 100 ||
        this.newgrade < 0
      ) {
        this.$message({
          message: "分数只能是0-100的整数",
          type: "warning"
        });
      } else {
        this.tableData[this.gradelen].grade = this.newgrade;
        this.gradelen++;
        this.newgrade = "";
        this.isok = false;
        this.disinput = true;
        this.appitem = (this.appitem + 1) % this.appdetail.length;
        this.index = (this.index + 1) % musicList.length;
        this.url = musicList[this.index];
        setTimeout(() => {
          this.isok = true;
          this.disinput = false;
        }, this.appdetail[this.appitem % this.appdetail.length].time + 4000);
      }

      if (this.gradelen === this.tableData.length) {
        this.isbtnshow = true;
        this.isinput = false;
        this.isok = false;
      }
    },

    getDate() {
      for (let i = 0, len = this.tableData.length; i < len; i++) {
        this.dataarr.push({
          qualityLossRatio: this.tableData[i].qualityLossRatio,
          time: this.tableData[i].time,
          grade: this.tableData[i].grade
        });
      }
      this.dataarr = JSON.stringify(this.dataarr);
      return this.dataarr;
    },
    goVideo() {
      this.alldata = this.getDate();
      fetch("http://120.79.193.101/ApproximateComputing/audio/audios", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: this.alldata
      }).then(response => {
        return response.json().then(data => {
          if (data.code === 0) {
            this.$message({
              showClose: true,
              message: "提交成功",
              type: "success"
            });
            this.$store.commit("iscomplete");
            this.$router.push({
              path: "Video"
            });
            this.$store.commit("nocomplete");
          } else {
            this.$message({
              showClose: warning,
              message: "提交错误",
              type: "warning"
            });
          }
        });
      });
    }
  },
  watch: {
    url(newUrl) {
      this.$refs.audio.src = newUrl;
      this.$refs.audio.play();
    }
  }
};
</script>
<style scoped>
audio {
  margin-top: 150px;
}
</style>



