import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Audio from '@/components/Audio'
import Video from '@/components/Video'
import ImageRecognition from '@/components/ImageRecognition'
import SpeechRecognition from '@/components/SpeechRecognition'
import PhotoDecode from '@/components/PhotoDecode'
import PathPlaning from '@/components/PathPlaning'
import StockPrediction from '@/components/StockPrediction'
import TSP from '@/components/TSP'
import Thanks from '@/components/Thanks'
import AudioAD from '@/components/AudioAD'
import VideoAD from '@/components/VideoAD'
import ImageRecognitionAD from '@/components/ImageRecognitionAD'
import SpeechRecognitionAD from '@/components/SpeechRecognitionAD'
import PhotoDecodeAD from '@/components/PhotoDecodeAD'
import PathPlaningAD from '@/components/PathPlaningAD'
import StockPredictionAD from '@/components/StockPredictionAD'
import TSPAD from '@/components/TSPAD'

Vue.use(Router)

export default new Router({

  routes: [{
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '近似计算统计系统'
      }
    },
    {
      path: '/audio',
      name: 'Audio',
      component: Audio,
      meta: {
        title: '音频评分'
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/') {
          next()
        }
      }
    },
    {
      path: '/video',
      name: 'Video',
      component: Video,
      meta: {
        title: '视频评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/Audio') {
          next()
        }
      }
    },
    {
      path: '/imageRecognition',
      name: 'ImageRecognition',
      component: ImageRecognition,
      meta: {
        title: '图像识别评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/Video') {
          next()
        }
      }
    },
    {
      path: '/speechRecognition',
      name: 'SpeechRecognition',
      component: SpeechRecognition,
      meta: {
        title: '语音识别评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/ImageRecognition') {
          next()
        }
      }
    },
    {
      path: '/photoDecode',
      name: 'PhotoDecode',
      component: PhotoDecode,
      meta: {
        title: '高清图像解码评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/SpeechRecognition') {
          next()
        }
      }
    },
    {
      path: '/pathPlaning',
      name: 'PathPlaning',
      component: PathPlaning,
      meta: {
        title: '路径规划评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/PhotoDecode') {
          next()
        }
      }
    },
    {
      path: '/stockPrediction',
      name: 'StockPrediction',
      component: StockPrediction,
      meta: {
        title: '股票预测评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/PathPlaning') {
          next()
        }
      }
    },
    {
      path: '/tSP',
      name: 'TSP',
      component: TSP,
      meta: {
        title: '旅行商问题评分',
        full: true
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/StockPrediction') {
          next()
        }
      }
    },
    {
      path: '/thanks',
      name: 'Thanks',
      component: Thanks,
      meta: {
        title: '签名'
      },
      beforeEnter: (to, from, next) => {
        if (from.path === '/TSP') {
          next()
        }
      }
    },
    {
      path: '/audioAD',
      name: 'AudioAD',
      component: AudioAD,
      meta: {
        title: '管理员',
        requireAuth: true
      }
    },
    {
      path: '/videoAD',
      name: 'VideoAD',
      component: VideoAD,
      meta: {
        title: '管理员',
        requireAuth:true

      }
    },
    {
      path: '/imageRecognitionAD',
      name: 'ImageRecognitionAD',
      component: ImageRecognitionAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    },
    {
      path: '/speechRecognitionAD',
      name: 'SpeechRecognitionAD',
      component: SpeechRecognitionAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    },
    {
      path: '/photoDecodeAD',
      name: 'PhotoDecodeAD',
      component: PhotoDecodeAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    },
    {
      path: '/pathPlaningAD',
      name: 'PathPlaningAD',
      component: PathPlaningAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    },
    {
      path: '/stockPredictionAD',
      name: 'StockPredictionAD',
      component: StockPredictionAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    },
    {
      path: '/tSPAD',
      name: 'TSPAD',
      component: TSPAD,
      meta: {
        title: '管理员',
        requireAuth:true
      }
    }
  ]
})



