<template>
  <div>
    <appheader></appheader>
    <div class="container">
      <div class="Appdisplay">
        <div class="edge">
          <div class="desktop">

            <div class="showplace"
             v-loading="isloading"
    element-loading-text="图片解码中"
    element-loading-background="rgba(0, 0, 0, 0.8)">
              <img ref="img" class="myimg" src="../../static/image/pd0.jpg" alt="" v-show="isdecode">
              <img ref="img1" class="myimg" src="../../static/image/pd4.jpg"  alt="" v-show="isdecode">
              <img ref="img2" class="myimg" src="../../static/image/pd8.jpg"  alt="" v-show="isdecode">
              <img ref="img3" class="myimg" src="../../static/image/pd12.jpg"  alt="" v-show="isdecode">
            </div>
            <span class="detail">{{detail}}</span>
          </div>
        </div>

      </div>
      <div class="setgradewarp" v-if="isinput">
        <span>当前内容分数：</span>
        <input class="setgradeinput" placeholder="输入分数0~100" v-model="newgrade" :disabled='disinput'>
        <el-button type="primary" @click="setgradeDone" v-if="isok">确定</el-button>
      </div>
      <v-table class="gradetable" :width="400" :columns="columns" :table-data="tableData" :show-vertical-border="false">
      </v-table>
    </div>
    <el-button class="uploadgrade" v-if="isbtnshow" @click="goPathPlaning" type="success">提交所有分数</el-button>
  </div>
</template>
<script>
import appheader from "./AppHeader";
const imgList = [
  "./static/image/pd0.jpg",
  "./static/image/pd1.jpg",
  "./static/image/pd2.jpg",
  "./static/image/pd3.jpg"
];
const imgList1 = [
  "./static/image/pd4.jpg",
  "./static/image/pd5.jpg",
  "./static/image/pd6.jpg",
  "./static/image/pd7.jpg"
];
const imgList2 = [
  "./static/image/pd8.jpg",
  "./static/image/pd9.jpg",
  "./static/image/pd10.jpg",
  "./static/image/pd11.jpg"
];
const imgList3 = [
  "./static/image/pd12.jpg",
  "./static/image/pd13.jpg",
  "./static/image/pd14.jpg",
  "./static/image/pd15.jpg"
];
export default {
  name: "PhotoDecode",
  data() {
    return {
      tableData: [
        //app数据
        { qualityLossRatio: "0", time: "0", grade: "" },
        { qualityLossRatio: "10", time: "0", grade: "" },
        { qualityLossRatio: "25", time: "0", grade: "" },
        { qualityLossRatio: "50", time: "0", grade: "" },

        { qualityLossRatio: "0", time: "500", grade: "" },
        { qualityLossRatio: "10", time: "500", grade: "" },
        { qualityLossRatio: "25", time: "500", grade: "" },
        { qualityLossRatio: "50", time: "500", grade: "" },

        { qualityLossRatio: "0", time: "1000", grade: "" },
        { qualityLossRatio: "10", time: "1000", grade: "" },
        { qualityLossRatio: "25", time: "1000", grade: "" },
        { qualityLossRatio: "50", time: "1000", grade: "" },

        { qualityLossRatio: "0", time: "1500", grade: "" },
        { qualityLossRatio: "10", time: "1500", grade: "" },
        { qualityLossRatio: "25", time: "1500", grade: "" },
        { qualityLossRatio: "50", time: "1500", grade: "" }
      ],
      columns: [
        {
          field: "qualityLossRatio",
          title: "质量损失%",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          formatter(rowData, rowIndex, pagingIndex, field) {
            return `<span class='cell-edit-color'>${rowData[field]}</span>`;
          },
          isResize: true
        },
        {
          field: "time",
          title: "解码时间ms",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "grade",
          title: "评分：",
          width: 200,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        }
      ],
      gradelen: 0,
      dataarr: [],
      reg: /^(0|[1-9][0-9]*)$/,
      newgrade: "",
      isbtnshow: false,
      isinput: true,
      index: 0,
      url: "",
      url1: "",
      url2: "",
      url3: "",
      appdetail: [
        {
          title: "质量损失0% 解码时间0ms",
          time: 0
        },
        {
          title: "质量损失10% 解码时间0ms",
          time: 0
        },
        {
          title: "质量损失25% 解码时间0ms",
          time: 0
        },
        {
          title: "质量损失50% 解码时间0ms",
          time: 0
        },
        {
          title: "质量损失0% 解码时间500ms",
          time: 500
        },
        {
          title: "质量损失10% 解码时间500ms",
          time: 500
        },
        {
          title: "质量损失25% 解码时间500ms",
          time: 500
        },
        {
          title: "质量损失50% 解码时间500ms",
          time: 500
        },
        {
          title: "质量损失0% 解码时间1000ms",
          time: 1000
        },

        {
          title: "质量损失10% 解码时间1000ms",
          time: 1000
        },
        {
          title: "质量损失25% 解码时间1000ms",
          time: 1000
        },
        {
          title: "质量损失50% 解码时间1000ms",
          time: 1000
        },
        {
          title: "质量损失0% 解码时间1500ms",
          time: 1500
        },

        {
          title: "质量损失10% 解码时间1500ms",
          time: 1500
        },

        {
          title: "质量损失25% 解码时间1500ms",
          time: 1500
        },

        {
          title: "质量损失50% 解码时间1500ms",
          time: 1500
        }
      ],
      appitem: 0,
      isloading: false,
      isok: true,
      disinput: false,
      alldata: "",
      isdecode: true
    };
  },
  components: {
    appheader
  },
  computed: {
    detail() {
      return this.appdetail[this.appitem].title;
    }
  },
  methods: {
    setgradeDone() {
      if (
        !this.reg.test(this.newgrade) ||
        this.newgrade > 100 ||
        this.newgrade < 0
      ) {
        this.$message({
          message: "分数只能是0-100的整数",
          type: "warning"
        });
      } else {
        this.tableData[this.gradelen].grade = this.newgrade;
        this.gradelen++;
        this.newgrade = "";
        this.isok = false;
        this.disinput = true;
        this.isloading = true;
        this.appitem = (this.appitem + 1) % this.appdetail.length;
        this.index = (this.index + 1) % imgList.length;
        this.url = imgList[this.index];
        this.url1 = imgList1[this.index];
        this.url2 = imgList2[this.index];
        this.url3 = imgList3[this.index];
        this.isdecode = false;

        setTimeout(() => {
          this.isdecode = true;
          this.isloading = false;
          this.isok = true;
          this.disinput = false;
        }, this.appdetail[this.appitem % this.appdetail.length].time);
      }

      if (this.gradelen === this.tableData.length) {
        this.isbtnshow = true;
        this.isinput = false;
        this.isok = false;
      }
    },

    getDate() {
      for (let i = 0, len = this.tableData.length; i < len; i++) {
        this.dataarr.push({
          qualityLossRatio: this.tableData[i].qualityLossRatio,
          time: this.tableData[i].time,
          grade: this.tableData[i].grade
        });
      }
      this.dataarr = JSON.stringify(this.dataarr);
      return this.dataarr;
    },
    goPathPlaning() {
      this.alldata = this.getDate();

      fetch(
        "http://120.79.193.101/ApproximateComputing/photoDecode/photoDecodes",
        {
          method: "post",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: this.alldata
        }
      ).then(response => {
        return response.json().then(data => {
          if (data.code === 0) {
            this.$message({
              showClose: true,
              message: "提交成功",
              type: "success"
            });
            this.$store.commit("iscomplete");
            this.$router.push({
              path: "PathPlaning"
            });
            this.$store.commit("nocomplete");
          } else {
            this.$message({
              showClose: warning,
              message: "提交错误",
              type: "warning"
            });
          }
        });
      });
    }
  },
  watch: {
    url(newUrl) {
      this.$refs.img.src = newUrl;
    },
    url1(newUrl) {
      this.$refs.img1.src = newUrl;
    },
    url2(newUrl) {
      this.$refs.img2.src = newUrl;
    },
    url3(newUrl) {
      this.$refs.img3.src = newUrl;
    }
  }
};
</script>
<style scoped >
.myimg {
  width: 240px;
  height: 140px;
}

.showplace {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
</style>



