<template>
<div >
  <p>谢谢你的参与!</p>
  <div class="name" v-show="isshow">
  <b class="tips">为知晓你已经完成,请留下你的大名，这个很重要！</b>
  <el-input v-model="students" placeholder="请输入真实姓名" class="nameinput"></el-input>
   <el-button @click="submit">提交</el-button>
   </div>
</div>
</template>
<script>
export default {
  name: "Thanks",
  data() {
    return {
      students: "",
      isshow: true
    };
  },
  methods: {
    submit() {
      console.log(this.students);

      if (this.students === "") {
        this.$message({
          message: "名字不能为空",
          type: "warning"
        });
      } else {
        fetch(
          "http://120.79.193.101/ApproximateComputing/applicant/applicant",
          {
            method: "post",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              name: this.students
            })
          }
        ).then(response => {
          return response.json().then(data => {
            if (data.code === 0) {
              this.isshow = false;
              this.$message({
                showClose: true,
                message: "提交成功",
                type: "success"
              });
            }
          });
        });
      }
    }
  }
};
</script>
<style scoped>
p {
  font-size: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -140%);
}
.tips {
  color: red;
}
.nameinput {
  width: 200px;
}
.name {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -70%);
}
</style>



